import {Component, EventEmitter, Input, Output} from "@angular/core";
import {TreeNode} from "@component/inventory/tree-view/tree-node";
import ObjectInfo from "@component/inventory/tree-view/ObjectInfo";

/**
 * A dropdown wrapper for vsan-tree-node component. It allows selection of one or more managed objects.
 */
@Component({
   selector: "vsan-dropdown-object-selector",
   templateUrl: "dropdown-object-selector.html",
   styleUrls: ["dropdown-object-selector.scss"]
})
export class DropdownObjectSelectorComponent {
   @Input()
   label: string;

   @Input()
   nodes: TreeNode[];

   @Input()
   searchInputAriaLabelKey: string;

   @Input()
   allowMultipleSelection = false;

   @Input()
   showSearch: boolean = true;

   /**
    * The objects can be preselected.
    */
   @Input()
   selectedObjectInfos: ObjectInfo[];

   @Output()
   selectedObjectInfosChange: EventEmitter<ObjectInfo[]> = new EventEmitter<ObjectInfo[]>();

   menuOpen: boolean = false;

   onSelectedObjectInfosChange(objectInfos: ObjectInfo[]) {
      this.selectedObjectInfosChange.emit(objectInfos);
      if (!this.allowMultipleSelection) {
         this.menuOpen = false;
      }
   }

   get singleSelectionObjectInfo(): ObjectInfo {
      return this.selectedObjectInfos && this.selectedObjectInfos[0]
            ? this.selectedObjectInfos[0]
            : null;
   }
}
